<template>
  <div>
    <table>
      <tbody>
      <tr v-for="(row,idx) in grid" :key="idx">
        <td v-for="(cell,idy) in row" :key="idy" 
        :class="{ 
          locked: grid[idx][idy].locked, 
          selected:grid[idx][idy].selected,
          error:grid[idx][idy].error
        }"
        @click="setSelected(grid[idx][idy], idx, idy)"> {{ grid[idx][idy].value }}</td>
      </tr>
      </tbody>
    </table>
    <p/>
    <b-button pill v-for="i in 9" v-bind:key="i" variant="success" class="mr-1" @click="pickNumber1(i)">{{ i }}</b-button>
    <b-button pill variant="success" class="mr-1" @click="pickNumber1('')">Clear</b-button>
    <p/>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Grid',
  computed: mapState('sudoku', [
    'grid'
  ]),
  methods: {
    pickNumber(e) {
      let typed = parseInt(String.fromCharCode(e.keyCode),10);
      // if it was NaN, split out
      if(!typed) return;
      this.$store.commit('sudoku/setNumber', typed);
    },
    pickNumber1(i) {
      this.$store.commit('sudoku/setNumber', i);
    },
    setSelected(cell,x,y) {
      this.$store.commit('sudoku/setSelected',{x,y});
    }
  },
  mounted() {
    window.addEventListener('keypress', this.pickNumber);
  },
  destroyed() {
    window.removeEventListener('keypress', this.pickNumber);
  }
}
</script>

