<template>
  <div id="sudoku">
    <b-container fluid>
      <b-row
        ><b-col cols="12" align="center">
          <h1>Sudoku</h1>
        </b-col>
      </b-row>
      <b-row
        ><b-col cols="12" align="center">
          <div v-if="wonGame">
            <h2 class="wonGame">YOU WON!</h2>
          </div>
        </b-col>
      </b-row>
      <b-row
        ><b-col cols="12" align="center">
          <Grid />
        </b-col>
      </b-row>
      <p>
        <b-row align="center">
          <b-col cols="12">
            <select v-model="difficulty">
              <option v-for="(difficulty, idx) in difficulties" :key="idx">
                {{ difficulty }}
              </option>
            </select>
            <b-button
              @click="newGame"
              variant="primary"
              size="sm"
              class="ml-1 mr-1"
              >New Game</b-button
            >
            Time: <b>{{ toHHMMSS(timeInSeconds) }}</b>
          </b-col>
        </b-row>
      </p>
    </b-container>
  </div>
</template>

<script>
import { mapState } from "vuex";

import Grid from "@/components/SudokuGrid";

export default {
  name: "sudoku",
  components: {
    Grid,
  },
  data() {
    return {
      difficulty: null,
    };
  },
  computed: mapState("sudoku", ["difficulties", "wonGame", "timeInSeconds"]),

  created() {
    this.$store.dispatch("sudoku/generatePuzzle", "easy");
    this.difficulty = this.difficulties[0];
  },
  methods: {
    newGame() {
      this.$store.dispatch("sudoku/generatePuzzle", this.difficulty);
      this.$store.dispatch("sudoku/startGame");
    },
    toHHMMSS(secs) {
      var sec_num = parseInt(secs, 10);
      var hours = Math.floor(sec_num / 3600);
      var minutes = Math.floor(sec_num / 60) % 60;
      var seconds = sec_num % 60;

      return [hours, minutes, seconds]
        .map((v) => (v < 10 ? "0" + v : v))
        .filter((v, i) => v !== "00" || i > 0)
        .join(":");
    },
  },
};
</script>

<style>
body {
  font-family: Arial, Helvetica, sans-serif;
}

.wonGame {
  color: green;
  text-decoration: underline;
}
</style>
